.container{
	background: rgb(25, 25, 40);
	color: rgb(255, 255, 255);
}

.container h1{
	width: 100%;
	font-family: 'Playfair Display', serif;
	font-size: 50px;
	display: inline;
}

.container h2{
	width: 100%;
	font-family: 'Playfair Display', serif;
	font-size: 30px;
	display: inline;
	margin-right: 20px;
}

.container img{
	height:80px;
	cursor: pointer;
}

.container a{
	margin-right: 20px;
}

.line{
	margin-top: -25px;
	margin-left: 300px;
	display: block;
	width: 40%;
	height: 4px;
	background: linear-gradient(0.25turn,#ffffff, #ffffff00);	margin-bottom: 100px;
}

.content {
	margin-left: 100px;
	margin-bottom: 130px;
}

.title{
	margin-bottom: 150px;
}

.contact{
	width: 100%;
	font-size: 24px;
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
	margin-bottom: 40px;
}

.footer{
	text-align: center;
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
	padding-bottom: 5px;
}

@media (max-width: 767px){
	.content{
	margin-left: 0;
	}

	.line{
		width: 25%;
	}
}

@media (max-width: 424px){
	.container h1{
		font-size: 36px;
	}
	.line{
		width: 30%;
		margin-top: -15px;
		margin-left: 220px;
	}
	.contact{
		font-size: 18px;
	}
	.container img{
		height:55px;
	}
}