.container{
	background: rgb(25, 25, 40);
	color: rgb(255, 255, 255);
	width: 100%;
	margin-bottom: 200px;
}

.title h1{
	font-family: 'Playfair Display', serif;
	font-size: 50px;
}

.line{
	margin-top: -25px;
	margin-left: 300px;
	display: block;
	width: 40%;
	height: 4px;
	background: linear-gradient(0.25turn,#ffffff, #ffffff00);	margin-bottom: 100px;
}


.title{
	margin-bottom: 150px;
}

.content h1{
	font-family: 'Playfair Display', serif;
	font-size: 40px;
	text-align: center;
	margin-bottom: 100px;
}

.other {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

@media (max-width: 767px){
	.line{
		width: 25%;
	}
}

@media (max-width: 424px){
	.container h1{
		font-size: 36px;
	}
	.line{
		width: 30%;
		margin-top: -15px;
		margin-left: 220px;
	}
}