.container{
	background: rgb(25, 25, 40);
	color: rgb(255, 255, 255);
	margin-bottom: 100px;
}

.container h1{
	width: 100%;
	font-family: 'Playfair Display', serif;
	font-size: 50px;
	display: inline;
}

.line{
	margin-top: -25px;
	margin-left: 250px;
	display: block;
	width: 40%;
	height: 4px;
	background: linear-gradient(0.25turn,#ffffff, #ffffff00);	margin-bottom: 100px;
}

.content p{
	font-family: 'Nunito', sans-serif;
	font-size: 24px;
	font-weight: 200;
	line-height: 40px;
}

.content img{
	margin: 20px;
	border-radius: 50%;
	border: 1px solid rgb(0, 25, 255);
	height: 300px;
	box-shadow: 0px 0px 20px rgb(0, 25, 255);
}

.container span{
	text-decoration: underline;
	text-decoration-color:rgb(0, 25, 255) ;
}

.content{
	justify-content: space-between;
	align-items: center;
	display: flex;
}

@media (max-width: 767px){
	.content{
		flex-direction:column-reverse;

	}
}

@media (max-width: 424px){
	.container h1{
		font-size: 36px;
	}
	.line{
		margin-top: -20px;
		margin-left: 200px;
		width: 30%;
	}
}