.container{
	background: rgb(25, 25, 40);
	color: rgb(255, 255, 255);
	margin-bottom: 100px;
}
.container h1{
	font-family: 'Playfair Display', serif;
	font-size: 50px;
}

.container img{
	height: 100px;
	margin-bottom: 20px;
}

.container p{
	font-family: 'Nunito', sans-serif;
	font-size: 30px;
	text-align: center;
}

.item{
	width: 100px;
	margin: 40px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.line{
	margin-top: -25px;
	margin-left: 400px;
	display: block;
	width: 40%;
	height: 4px;
	background: linear-gradient(0.25turn,#ffffff, #ffffff00);	margin-bottom: 100px;
}

.content {
	margin: 0 auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.title{
	margin-bottom: 150px;
}

@media (max-width: 767px){
	.line{
	margin-left: 180px;
	width: 40%;
	}
	.container h1{
		width: 50%;
	}
}

@media (max-width: 424px){
	.container h1{
		font-size: 36px;
	}
	.line{
		margin-top: -15px;
		margin-left: 180px;
	}
}