.container{
	width: 300px;
	border: 3px solid rgb(0, 25, 255);
	border-radius: 20px;
	padding: 20px;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	transition: all ease 0.5s;
	margin-bottom: 30px;
}

.discr h2{
	font-family: 'Playfair Display', serif;
	font-size: 30px;
	font-weight: 600;
	margin-bottom: 30px;
	word-spacing: 5px;
}

.discr p{
	width: 100%;
	display: inline;
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
	font-size: 20px;
}

.discr{
	margin-bottom: 100px;
}

.tech{
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
	font-size: 18px;
	word-spacing: 10px;
	display: flex;
	justify-content: right;
	align-items: center;
}

.tech img{
	margin-left: 20px;
	width: 30px;
	cursor: pointer;
}

.container:hover {
	margin: -5px 5px 35px -5px;
	transition: all ease 0.3s;
	box-shadow: 5px 5px 0 rgb(0, 25, 255);
}