.menu.active{
	transition: all ease 1s;
	transform: translateY(-110%);
}

.menu {
	transition: all ease 1s;
	transform: translateY(0);
	position: fixed;	
	top: 70px;
	width: 100vw;
	height: 100vh;
	background-color: rgba(25, 25, 40,0.7);
	user-select: none;
	z-index: 10;
}

.menu ul{
	margin: 0 auto;
	max-width: 1200px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	color: rgb(255, 255, 255);
	font-size: 20px;
	font-family: 'Nunito', sans-serif;
	font-weight: normal;
}

.menu li {
	cursor: pointer;
	margin-bottom: 30px;
}

.menu li::after{
	display: block;
	content: '';
	width: 100%;
	border-bottom: 2px solid rgb(255, 255, 255);
	padding-top: 5px;
	opacity: 0;
	transition: all ease-in-out 0.4s;

}

.menu li:hover::after {
	opacity: 1;
}