.container{
	background: url(../../img/nature.jpg) no-repeat;
	background-position: center;
	background-size: cover;
	color: rgb(255, 255, 255);
	height: 110vh;
	cursor: default;
	box-shadow: inset 0 -200px 80px rgb(25, 25, 40);
	display: flex;
	justify-content: start;
	padding-top: 30vh;
	padding-left: 10vw;
	margin-bottom: 100px;
	max-width: 100%;
}

.container img{
	min-width:100%;
	min-height: 100vh;
	box-shadow: inset 0px 0px 100px rgba(0,0,0,0.9);
}

.title{
	text-shadow: 3px 3px 10px rgb(0, 0, 0);
}

.title p{
	font-family: 'Nunito', sans-serif;
	font-size: 32px;
	margin-bottom: 20px;
	user-select: none;
	font-weight: 300;
}

.title h1{
	font-family: 'Playfair Display', serif;
	font-size: 70px;
	margin-bottom: 100px;
}

.title h2 {
	width: 100%;
	font-family: 'Special Elite', cursive;
	font-size: 36px;
	user-select: none;
	overflow: hidden;
	border-right: 4px solid orange;
	white-space: nowrap;
	animation: 
	  typing 3.5s steps(30, end),
	  blink-caret .5s step-end infinite;
}

@keyframes typing {
	from { width: 0 }
	to { width: 100% }
}

@keyframes blink-caret {
	from, to { border-color: transparent }
	50% { border-color: orange }
}

@media (max-width: 767px){
	.title p{
		font-size: 20px;
	}
	.title h1{
		font-size: 38px;
	}
	.title h2{
		font-size: 18px;
		width: 330px;
	}
}

@media (max-width: 424px){
	.title p{
		font-size: 20px;
	}
	.title h1{
		font-size: 40px;
	}
	.title h2{
		font-size: 14px;
		width: 255px;
	}
}
