.container{
	position: fixed;
	width: 100%;
	height: 70px;
	user-select: none;
	text-shadow: 1px 1px 20px rgb(0, 0, 0);
	background-color: rgba(25, 25, 40,0.7);
	z-index: 10;
}

:any-link {
	text-decoration: none;
	cursor: pointer;
	color: rgb(255, 255, 255);
}

.container ul{
	margin: 0 auto;
	max-width: 1200px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	height: 100%;
	color: rgb(255, 255, 255);
	font-size: 20px;
	font-family: 'Nunito', sans-serif;
	font-weight: normal;
}

.container li {
	cursor: pointer;
}

.container li::after{
	display: block;
	content: '';
	width: 100%;
	border-bottom: 2px solid rgb(255, 255, 255);
	padding-top: 5px;
	opacity: 0;
	transition: all ease-in-out 0.4s;

}

.container li:hover::after {
	opacity: 1;
}

.btn{
	width: 30px;
	height: 25px;
	position: relative;
	cursor: pointer;
	display: none;
}

.btn:after{
	content: '';
	background-color: rgb(255, 255, 255);
	position: absolute;
	top: 2px;
	width: 30px;
	height: 1px;
}

.btn span{
	background-color: rgb(255, 255, 255);
	position: absolute;
	top: 12px;
	width: 30px;
	height: 1px;
}

.btn:before{
	content: '';
	background-color: rgb(255, 255, 255);
	position: absolute;
	bottom: 2px;
	width: 30px;
	height: 1px;
}

@media (max-width: 767px){
	.container li{
		display: none;
	}
	.btn{
		display: block;
	}
	.container ul {
		width: 30px;
		margin: 0 50px;
	}
	.container{
		display: flex;
		justify-content: end;
	}
}