/* Обнуляющий стиль */
* {
	padding: 0;
	margin: 0;
	border: 0;
	cursor: default;
}
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}
nav,
footer,
header,
aside {
	display: block;
}
html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}
input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}
/* ----------------------------------- */
@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;900&family=Playfair+Display:ital,wght@0,400;0,900;1,400;1,900&display=swap');
/* 
font-family: 'Special Elite', cursive;
font-family: 'Nunito', sans-serif;
font-family: 'Playfair Display', serif;
 */

body{
	font-family:Arial, Helvetica, sans-serif;
	background-color: rgb(25, 25, 40);
}

.container{
	width: 1000px;
	margin: 0 auto;
}

@media (max-width: 1200px){
	.container{
		width: 700px;
	}
}

@media (max-width: 767px){
	.container{
		width: 400px;
	}
}

@media (max-width: 424px){
	.container{
		width: 300px;
	}
}