.content h2{
	font-family: 'Playfair Display', serif;
	font-size: 40px;
	margin-bottom: 20px;
}

.content p{
	font-size: 20px;
	display: inline;
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
}

.content {
	margin-bottom: 130px;
}

.item{
	display: flex;
	align-items: center;
}

.img img{
	display: block;
	width: 600px;
	max-height:350px;
}

.img{
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 30px;
	overflow: hidden;
	opacity: 0.4;
	transition: all ease 0.5s;
}

.img:hover{
	transition: all ease 0.5s;
	opacity: 1;
}

.body{
	border-radius: 30px;
	background-color: rgb(255, 255, 255);
	border: 1px solid rgb(140, 140, 140);
	padding: 30px;
	margin-bottom: 10px;
}

.discription{
	text-align: right;
	z-index: 2;
	width: 550px;
	color: rgb(0, 0, 0);
	margin-left: -150px;
}

.tech{
	color:rgb(255, 255, 255);
	font-family: 'Nunito', sans-serif;
	font-weight: 200;
	font-size: 20px;
	margin-right: 30px;
	word-spacing: 20px;
	display: flex;
	align-items: center;
	justify-content: right;
}

.tech img{
	width: 30px;
	margin-left: 15px;
	cursor: pointer;
}